import {getQuery} from '../../services/getQuery.js'
import * as types from '../types.js'

const state = {
	Own:[],
	Banner:[]
}

const actions ={
[types.UPDATE_BANNER] : ({state,commit})=>getQuery({collectionName:'Banner',params:{_limit:3}}).then(data => data && commit(types.UPDATE_BANNER,data)),
[types.UPDATE_OWN]: ({state,commit}) => getQuery({collectionName:'Own'}).then(data => data && commit(types.UPDATE_OWN,data)),
}

const mutations ={
[types.UPDATE_BANNER] : (state,payload) => state.Banner = payload,
[types.UPDATE_OWN] : (state,payload) => state.Own = payload,
	
}

const getters ={
	
}

export default {
	actions,mutations,state,namespaced:true
}

