import {getQuery} from '../../services/getQuery.js'
import * as types from '../types.js'

const state ={
	User:[],
	Banner:[]
}


const actions = {
[types.UPDATE_BANNER] : ({state,commit})=>getQuery({collectionName:'Banner',params:{_limit:3}}).then(data => data && commit(types.UPDATE_BANNER,data)),[types.UPDATE_USER]: ({state,commit}) => getQuery({collectionName:'User'}).then(data => data && commit(types.UPDATE_USER,data)),}

const mutations = {
[types.UPDATE_USER] : (state,payload) => state.User = payload,
[types.UPDATE_BANNER] : (state,payload) => state.Banner = payload,}

const getters ={
	
}

export default {
	actions,mutations,state,namespaced:true
}