<template>
  <div class="cell">
	<div @click="toDetail">
	  <h3><span v-show="index != null">{{index + 1}}.</span>{{list.title}}</h3>
	  <p>{{list.des}}</p>
	  <a class="cell__anchor" v-if="isAdd" href="javascript:;" @click.stop="$emit('add-item',$event)">+</a>
	</div>
  </div>
</template>



<script>
export default {
  name: 'Cell',
  data(){
	  return{}
  },
  props: {
	  list:{
		  type:Object,
		  required:true
	  },
	  collectionName:{
	    type:String,
	    default:null
	  },
	  isAdd:{
	    type:Boolean,
	    default:false
	  },
	  index:{
	    type:Number,
	    default:null
	  },
	  
  },
  methods:{
	  toDetail(){
		  if(this.collectionName){
		    this.$router.push(`/Detail/${this.list._id}?collectionName=${this.collectionName}`)
	  }
	}
  },
  components:{
	  
  },
}
</script>

<style scoped>
 .cell{position: relative; }
  .cell:active {background: #ccc;}
  .cell div { color:#494d4d; padding:0.2rem 0; border-bottom:1px dashed #ccc;}
  .cell div h2{max-height:0.9rem; font-size:0.33rem; overflow:hidden;}
  .cell div p{max-height:1.8rem; margin-top:0.05rem;overflow:hidden;}
  .cell__anchor{position:absolute;right:0.3rem;top:0.4rem;}
</style>
