import * as types from './types.js';

let mutations = {
 [types.UPDATE_ISLOADING] : (state,payload) => state.isLoading = payload,
 [types.UPDATE_ISNAV] : (state,payload) => state.isNav = payload,
 [types.UPDATE_ISFOOTER] : (state,payload) => state.isFooter = payload,
}

export default mutations;