import Vue from "vue";
import Vuex from "vuex";
Vue.use(Vuex);

import actions from "../store/actions.js";
import mutations from "../store/mutations.js"
import state from "../store/state.js"
import getters from "../store/getters.js"

import About from '../store/medules/About.js'
import Business from '../store/medules/Business.js'
import Detail from '../store/medules/Detail.js'
import Own from '../store/medules/Own.js'
import User from '../store/medules/User.js'

let store = new Vuex.Store({
	actions,mutations,state,getters,
	modules:{
		About,Business,Detail,Own,User
	}
})
export default store
