import {getQuery} from '../../services/getQuery.js'
import * as types from '../types.js'


const state ={
	 Detail: {}
}
const actions = {
	[types.UPDATE_DETAIL]: ({state,commit},{_id,collectionName}) => getQuery({collectionName,_id}).then(res => commit(types.UPDATE_DETAIL,res))
	
}
const mutations = {
	[types.UPDATE_DETAIL] : (state,payload) => state.Detail = payload,
	
}
const getters ={
	
}

export default {
	actions,mutations,state,namespaced:true
}