<template>
	<div id="app">
		<Loading v-show="$store.state.isLoading"></Loading>
		<Header v-show="$store.state.isNav"></Header>
		<router-view />
		<Footer v-show="$store.state.isFooter"></Footer>
	</div>
</template>

<script>
import Header from './Header.vue';
import Footer from './Footer.vue';
import Loading from '../components/Loading.vue';
import * as types from '../store/types.js'
export default {
	name: 'App',
	data() {
		return {};
	},
	components: {
		Header,
		Footer,
		Loading
	},
	methods: {},
	watch: {
		$route: {
			handler(nextRouter){
				let path = nextRouter.path;
				
				if (/User|Own|Business/.test(path)) {
					this.$store.commit(types.UPDATE_ISNAV,true)
					this.$store.commit(types.UPDATE_ISFOOTER,true)
				}
				if (/Login|Reg|Detail|NoPage/.test(path)) {
					this.$store.commit(types.UPDATE_ISNAV,false)
					this.$store.commit(types.UPDATE_ISFOOTER,false)
				}
				if (/About/.test(path)) {
					this.$store.commit(types.UPDATE_ISNAV,false)
					this.$store.commit(types.UPDATE_ISFOOTER,true)
				}
			},
			immediate: true
		},
		
	},
	mounted() {
	},
	updated() {}
};
</script>

<style>
#app {
	height: 100%;
}
</style>
